// html,
.container-carousel {
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
    font-size: 3vmin;
  }
  
  // html {
  //   background: #151515;
  //   color: #fff;
  //   overflow: hidden;
  // }
  
  .container-carousel {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #151515;
    color: #fff;
    overflow: hidden;
    animation: Show 2.5s ;
  }
  
  .slides {
    display: grid;
    > .slide {
      grid-area: 1 / -1;
    }
  
    > button {
      appearance: none;
      background: transparent;
      border: none;
      color: white;
      position: absolute;
      font-size: 5rem;
      width: 5rem;
      height: 5rem;
      top: 30%;
      transition: opacity 0.3s;
      opacity: 0.7;
      z-index: 5;
  
      &:hover {
        opacity: 1;
      }
  
      &:focus {
        outline: none;
      }
  
      &:first-child {
        left: -50%;
      }
      &:last-child {
        right: -50%;
      }
    }
  }
  
  .slide {
    //transform-style: preserve-3d;
    // border: solid 1px red;
  
    // &[data-active] {
    //   .slideContent > * {
    //     transform: none;
    //     opacity: 1;
    //   }
    // }
  }
  
  .slideContent {
    width: 30vw;
    height: 40vw;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    transition: transform 0.5s ease-in-out;
    opacity: 0.7;
  
    display: grid;
    align-content: center;
  
    transform-style: preserve-3d;
    transform: perspective(1000px) translateX(calc(100% * var(--offset)))
      rotateY(calc(-45deg * var(--dir)));
  }
  
  .slideContentInner {
    display: none;
    transform-style: preserve-3d;
    transform: translateZ(2rem);
    transition: opacity 0.3s linear;
    text-shadow: 0 0.1rem 1rem #000;
    opacity: 0;
  
    .slideSubtitle,
    .slideTitle {
      font-size: 2rem;
      font-weight: normal;
      letter-spacing: 0.2ch;
      text-transform: uppercase;
      margin: 0;
    }
  
    .slideSubtitle::before {
      content: "— ";
    }
  
    .slideDescription {
      margin: 0;
      font-size: 0.8rem;
      letter-spacing: 0.2ch;
    }
  }
  
  // .slideBackground {
  //   position: fixed;
  //   top: 0;
  //   left: -10%;
  //   right: -10%;
  //   bottom: 0;
  //   background-size: cover;
  //   background-position: center center;
  //   z-index: -1;
  //   opacity: 0;
  //   transition: opacity 0.3s linear, transform 0.3s ease-in-out;
  //   pointer-events: none;
  
  //   transform: translateX(calc(10% * var(--dir)));
  // }
  
  .slide[data-active] {
    z-index: 2;
    pointer-events: auto;
  
    .slideBackground {
      opacity: 0.2;
      transform: none;
    }
  
    .slideContentInner {
      display: block;
      opacity: 1;
    }
  
    .slideContent {
      --x: calc(var(--px) - 0.5);
      --y: calc(var(--py) - 0.5);
      opacity: 1;
  
      transform: perspective(1000px);
  
      &:hover {
        transition: none;
        transform: perspective(1000px) rotateY(calc(var(--x) * 45deg))
          rotateX(calc(var(--y) * -45deg));
      }
    }
  }

  .muaVeNhanh{
    bottom: -5%;
    background: $color-while-0;
    border-radius: 10px;
    left: 50%;
    transform: translateX(-50%);
    box-shadow: 4px 4px 18px $color-black-3;
    z-index: 20;
  }
  
@keyframes Show {
  from {opacity: 0;}
  to {opacity:1;}
}